let _this;

export default {
  data() {
    return {
      arrList: [],
      userInfo: "",
      isIdentity: false,
      //是否已经有身份
      inAudit: false,
      //是否审核中
      identityName: '',
      regionalManager: false,
      //是否是区域经理
      newsNum: 0,
      //新消息数量
      popAds: false
    };
  },

  onPullDownRefresh: function() {
    this.getUserInfo();
    this.getNewsNum();
    this.popAds();
  },

  onShow: function (e) {
    let register = wx.getStorageSync("register");

    if (register) {
      this.getUserInfo();
      wx.removeStorageSync("register");
    }

    if (wx.getStorageSync("token")) {
      this.getNewsNum();
    }
  },
  onLoad: function (options) {
    wx.showLoading({
      title: '加载中'
    });
    let timer = setTimeout(() => {
      wx.hideLoading();
    }, 3000);
    _this = this;

    if (wx.getStorageSync("token")) {
      this.setUserInfo();
      this.getUserInfo();
      this.popAds();
    } else {
      wx.hideLoading();
      User.loginByWeixin().then(res => {
        _this.setUserInfo();

        _this.getUserInfo();

        _this.popAds();
      }).catch(err => {
        console.log("err=>", err);
      });
    }
  },
  components: {},
  props: {},
  methods: {
    // 获取未读消息数量
    getNewsNum: function () {
      JsyServer.getNewsNum().then(res => {
        _this.setData({
          newsNum: res.data
        });
      }).catch(err => {
        console.log("getNewsNum=err==", res);
      });
    },
    getUserInfo: function () {
      wx.showLoading({
        title: '加载中'
      });
      wx.stopPullDownRefresh();
      JsyServer.getUserInfo().then(res => {
        // if (res.data.authority.length != 0 && res.data.authority != "EXAMINE") {
        console.log("res----", res.data);
        wx.setStorageSync("userInfo", res.data);

        _this.setUserInfo(); // }

      }).catch(err => {
        wx.hideLoading();
        console.log("getUserInfoERR=>", err);
      });
    },
    setUserInfo: function () {
      let userInfo = wx.getStorageSync("userInfo");
      this.setData({
        userInfo: userInfo
      });

      if (userInfo.authority && userInfo.authority.length == 0) {
        this.setData({
          isIdentity: false
        });
      } else if (userInfo.authority[0].name == "EXAMINE") {
        this.setData({
          isIdentity: false,
          inAudit: true
        });
      } else {
        this.setData({
          isIdentity: true
        });
        let role = userInfo.authority[0].name;

        if (role == "ROLE_SELL_DEPUTY") {
          if (userInfo.authorityOther == 7) {
            this.setData({
              identityName: '卖帮办',
              regionalManager: true,
              arrList: arrListAdmin
            });
          } else {
            this.setData({
              identityName: '卖帮办',
              regionalManager: false,
              arrList: arrListBb
            });
          }
        } else if (role == "ROLE_BUY_DEPUTY") {
          if (userInfo.authorityOther == 7) {
            this.setData({
              identityName: '买帮办',
              regionalManager: true,
              arrList: arrListAdmin
            });
          } else {
            this.setData({
              identityName: '买帮办',
              regionalManager: false,
              arrList: arrListBb
            });
          }
        } else if (role == "ROLE_BUYER") {
          this.setData({
            identityName: '买家',
            regionalManager: false,
            arrList: arrListBuyer
          });
        } else if (role == "ROLE_SELLER") {
          this.setData({
            identityName: '卖家',
            regionalManager: false,
            arrList: arrListSeller
          });
        } else if (role == "ROLE_ANALYST") {
          this.setData({
            identityName: '分析师',
            regionalManager: false,
            arrList: arrListFx
          });
        } else if (role == "ROLE_SALES_DIRECTOR") {
          this.setData({
            identityName: '销售总监',
            regionalManager: false,
            arrList: arrListAdmin
          });
        } else if (role == "FREEZE") {
          this.setData({
            regionalManager: false,
            identityName: '已冻结',
            arrList: []
          });
        }
      }

      if (userInfo.authority && userInfo.authority.length == 2 && userInfo.authority[1].name == "ROLE_MANAGER") {
        let list = this.arrList;
        list.push({
          icon: '/images/jinsy/examine.png',
          name: '审核管理',
          url: '/pages/jin-suo-yun/customer-admin/bond-examine'
        });
        this.setData({
          arrList: list
        });
      }

      wx.hideLoading();
    },
    // 扫描二维码
    scanCode: function () {
      var that = this;
      wx.scanCode({
        success: function (res) {
          console.log("code---", res);
          console.log("path---", res.path);
          wx.navigateTo({
            url: "/" + res.path
          });
        },
        fail: function (res) {}
      });
    },
    navNewsPage: function () {
      console.log("跳转到消息页");
      wx.navigateTo({
        url: '/pages/jin-suo-yun/news'
      });
    },
    toChoicePage: function () {
      if (!wx.getStorageSync("token")) {
		 wx.showModal({
		 	title:'',
			content:'您的内部人员身份审核未通过，请选择其他身份进行注册。',
			success(res) {
				if (res.confirm) {
					wx.navigateTo({
					  url: '/pages/jin-suo-yun/choice-identity'
					});
				}
				else if (res.cancel) {
					console.log("取消页面！");
				}
				
			}
		 })
        return;
      }

      uni.getSetting({
        success(res) {
          if (res.authSetting['scope.userInfo']) {
            if (!_this.inAudit) {
              wx.showModal({
                title: '请先登录后再进行后续操作',
                content: '本次更新增加了身份信息，请登录并选择您的身份（身份只能选择一次，请谨慎选择）',

                success(res) {
                  if (res.confirm) {
                    wx.navigateTo({
                      url: '/pages/jin-suo-yun/choice-identity'
                    });
                  } else if (res.cancel) {
                    console.log('用户点击取消');
                  }
                }

              });
            } else {
              wx.showModal({
                title: '',
                content: '您的内部人员身份正在审核中'
              });
            }
          }
        }

      });
    },
    userInfo: function (e) {
      console.log(e);

      if (e.detail.errMsg.indexOf("getUserInfo:fail") != -1) {
        console.log("用户拒绝授权");
      } else {
        console.log("用户接受授权");

        if (!wx.getStorageSync("userInfo")) {
          _this.onLoad();
        }

        wx.setStorageSync("loginOn", false);
      }
    },
    toJump: function (e) {
      let url = e.currentTarget.dataset.url;

      if (!url || !this.popAds && url == "/pages/user-order/order") {
        wx.showToast({
          title: '开发中...',
          icon: 'none'
        });
      }
    },

//     /**
//     * 获取弹窗广告数据
//     */
    popAds: function (e) {
      JsyServer.controllShow().then(res => {
        console.log("res---", res);

        if (res.data == 1) {
          _this.setData({
            popAds: true
          });
        } else {
          _this.setData({
            popAds: false
          });
        }
      }).catch(err => {
        console.log("Err=>", err);
      });
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  } 
};
